<template>
  <div style="height: 100vh;">
    <transition name="scroll">
      <div class="background-scene1" v-if="showScene1">
        <div class="bottom-container"></div>
        <div class="img-container img-resize absolute-center">
          <img src="../assets/pot.svg" style="width: 100%; height: 100%"/>
        </div>
        <div class="selection-container absolute-center trans-A" ref="optionA" @click="click('A')">
          <transition name="fade">
            <span class="absolute-center" style="top: 0;" v-if="showSpan">A</span>
          </transition>
          <transition name="fade">
            <img src="../assets/food_1.png" v-if="showOption('A')" class="image"/>
          </transition>
        </div>
        <div class="selection-container absolute-center trans-B" ref="optionB" @click="click('B')">
          <transition name="fade">
            <span class="absolute-center" style="top: 0;" v-if="showSpan">B</span>
          </transition>
          <transition name="fade">
            <img src="../assets/food_2.png" v-if="showOption('B')" class="image"/>
          </transition>
        </div>
        <div class="selection-container absolute-center trans-C" ref="optionC" @click="click('C')">
          <transition name="fade">
            <span class="absolute-center" style="top: 0;" v-if="showSpan">C</span>
          </transition>
          <transition name="fade">
            <img src="../assets/food_3.png" v-if="showOption('C')" class="image"/>
          </transition>
        </div>
        <div class="selection-container absolute-center trans-D" ref="optionD" @click="click('D')">
          <transition name="fade">
            <span class="absolute-center" style="top: 0;" v-if="showSpan">D</span>
          </transition>
          <transition name="fade">
            <img src="../assets/food_4.png" v-if="showOption('D')" class="image"/>
          </transition>
        </div>
      </div>
    </transition>
    <div class="background-scene2">
      <transition name="fadeImg">
        <img src="../assets/record_player.svg" width="200px" height="200px" v-if="!showScene1"/>
      </transition>
      <transition name="fadeImg">
        <img src="../assets/record_cd.svg" width="200px" height="200px"
             class="rotate-cd" v-if="!showScene1"/>
      </transition>
      <transition name="fadeImg">
        <img src="../assets/record_pin.svg" width="200px" height="200px" v-if="!showScene1"/>
      </transition>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Forest',
  data() {
    return {
      showSpan: true,
      selected: '',
      showScene1: true,
    };
  },
  methods: {
    click(option) {
      this.showSpan = false;
      this.selected = option;
      this.$refs[`option${option}`].classList.add(`move-back-X-${option}`);
      setTimeout(() => {
        this.$refs[`option${option}`].classList.add('move-back-Y');
      }, 2000);
      setTimeout(() => {
        this.showScene1 = false;
      }, 4200);
    },
    showOption(option) {
      return this.selected === '' || this.selected === option;
    },
  },
};
</script>

